@use 'styles/colors';
@import 'styles/base';

:host {
  display: flex;
  background: #FFFFFF;

  box-shadow: 0 -10px 20px 5px rgba(0, 0, 0, 0.03);
  border-top: 1px solid #ededed;

  .page-block {
    @extend %page-block;

    padding: 60px 15px;
    //display: flex;
    //align-items: center;
    //justify-content: space-between;

    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
  }

  .labels {
     &__logo {
       display: block;
       position: relative;
       background-image: url('/assets/images/logotypes/logotype.png');
       background-size: contain;
       background-repeat: no-repeat;
       height: 35px;
       margin-bottom: 25px;
       opacity: 0.7;
     }

    &__text {
      @include proxima(14px, 400, colors.$foreground_dark);
      max-width: 450px;
    }
  }

  .links {
    display: flex;
    align-items: flex-start;
    gap: 60px;

    &-block {
      display: flex;
      flex-direction: column;

      &__title {
        @include proxima(18px, bold, colors.$foreground_dark);
        margin-bottom: 30px;
      }

      &__list {
        display: flex;
        flex-direction: column;
        gap: 10px;
      }

      &__item {
        @include proxima(14px, normal, colors.$foreground_dark);
        text-decoration: none;
      }
    }

  }

  @media screen and (max-width: 900px) {
    .page-block {
      flex-direction: column;
      gap: 50px;
    }
  }
}
